.container {
  color: #000;
  width: 500px;
  min-height: 200px;
  background-color: #9acd32;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: inset 0 0 40px #0009;
}

.img {
  width: 150px;
  height: 150px;
}

.joke {
  text-align: center;
  color: #000;
  opacity: 0;
  font-weight: 500;
}

.fade {
  opacity: 1;
  transition: opacity 1.5s;
}

.button {
  cursor: pointer;
  margin-top: 20px;
  padding: 10px 25px;
  font-weight: bold;
  position: relative;
  box-shadow: 5px 5px;
}

